.fixed {
  position: fixed !important;
  z-index: 200 !important;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 1.2rem 0.8rem 0rem rgba(34, 60, 80, 0.2);
  will-change: auto;
  transition: all 0.5s ease-out;

  @media (min-width: 1320px) {
    width: calc(100% - 17px) !important;
  }
}

//   
.fixed-wrap {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  will-change: auto;
  transition: all 0.5s ease-out;
}
